/*-------------------------
  User Profile Styles
-------------------------*/
.user-welcome-notice {
    margin-top: 60px;
    padding: 60px 0;
    background-color: rgba(232, 250, 250, 1);
    background: linear-gradient(90deg, rgba(14,180,180,0.06) 0%, rgba(246,113,111,0.06) 100%);
    .welcome-text {
        .title {
            margin-bottom: 0;
        }
    }
}

.user-profile-area {
    background: linear-gradient(-138.72deg, rgba(232, 250, 250, 0.7) -34.17%, rgba(255, 244, 245, 0.7) 126.35%);
    padding: 120px 0;
    @media only screen and (max-width: 991px) {
        padding: 100px 0;
    }
    @media only screen and (max-width: 767px) {
        padding: 80px 0;
    }
}


.user-profile-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: none;
    .nav-item {
        flex: 1;
        @media only screen and (max-width: 767px) {
            width: 33.33%;
            flex: auto;
        }
        .nav-link {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 0;
            font-size: 20px;
            font-weight: 500;
            color: $secondary_color;
            height: 80px;
            padding: 10px;
            text-align: center;
            background-color: #F8FAFC;
            border-bottom: 1px solid #DFE0E1;
            transition: .3s;
            @media only screen and (max-width: 991px) {
                font-size: 17px;
                height: 60px;
            }
            @media only screen and (max-width: 767px) {
                height: 50px;
            }
            .icon {
                display: none;
                @media only screen and (max-width: 767px) {
                    display: block;
                }
            }
            .text {
                @media only screen and (max-width: 767px) {
                    display: none;
                }
            }
            &:hover {
                color: $primary_color;
            }
            &.active {
                background-color: #fff;
                border: 1px solid #DFE0E1;
                border-bottom: none;
                color: $primary_color;
            }
        }
    }
}
   

.user-profile-box {
    padding: 40px;
    max-height: 600px;
    min-height: 300px;
    overflow-y: auto;
    background-color: #fff;
    @media only screen and (max-width: 767px) {
        padding: 30px;
    }
}

.user-profile {
    .heading-item {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        @media only screen and (max-width: 767px) {
            flex-direction: column-reverse;
        }
        .user-meta {
            display: flex;
            align-items: center;
            background-color: #F8FAFC;
            border-radius: 10px;
            .user-avatar {
                img {
                    border-radius: 10px;
                }
            }
            .user-name {
                padding: 24px;
                margin-bottom: 0;
                flex: 1;
            }
        }
        .action-btn {
            display: flex;
            @media only screen and (max-width: 767px) {
               margin-bottom: 30px;
            }
            button {
                margin-right: 12px;
                &:last-child {
                    margin-right: 0;
                }
            }
            .edit-btn {
                background-color: transparent;
                border: 1px solid #EDF2F7;
                border-radius: 6px;
                font-size: 12px;
                color: #16192C;
                height: 45px;
                line-height: 45px;
                padding: 0 20px;
                font-weight: 500;
                transition: .3s;
                i {
                    margin-right: 8px;
                }
                &:hover {
                    background-color: $primary_color;
                    border-color: $primary_color;
                    color: $paragraph_white;
                }
            }
            .submit-btn {
                font-size: 12px;
                letter-spacing: 0.0125em;
                height: 45px;
                line-height: 45px;
                padding: 0 20px;
            }
        }
    }
    .profile-info {
        .info-title {
            margin-bottom: 24px;
        }
    }
}